  export default "import React from 'react';\nimport Dialog from '@rc-component/dialog';\nimport 'bootstrap/dist/css/bootstrap.css';\nimport '../../assets/bootstrap.less';\n\n// Check for memo update should work\nconst InnerRender: React.FC = () => {\n  console.log('Updated...', Date.now());\n  return null;\n};\n\nconst MyControl: React.FC = () => {\n  const [visible, setVisible] = React.useState(false);\n  const [destroyOnHidden, setDestroyOnHidden] = React.useState(false);\n\n  const onClick = () => {\n    setVisible(true);\n  };\n\n  const onClose = () => {\n    setVisible(false);\n  };\n\n  const onDestroyOnHiddenChange = (e: React.ChangeEvent<HTMLInputElement>) => {\n    setDestroyOnHidden(e.target.checked);\n  };\n\n  const dialog = (\n    <Dialog\n      visible={visible}\n      destroyOnHidden={destroyOnHidden}\n      animation=\"slide-fade\"\n      maskAnimation=\"fade\"\n      onClose={onClose}\n      style={{ width: 600 }}\n      title={<div>第二个弹框</div>}\n      footer={[\n        <button type=\"button\" className=\"btn btn-default\" key=\"close\" onClick={onClose}>\n          Close\n        </button>,\n        <button type=\"button\" className=\"btn btn-primary\" key=\"save\" onClick={onClose}>\n          Save changes\n        </button>,\n      ]}\n    >\n      <InnerRender />\n      <h4>Text in a modal</h4>\n      <p>Duis mollis, est non commodo luctus, nisi erat porttitor ligula.</p>\n      <hr />\n      <h4>Overflowing text to show scroll behavior</h4>\n      <p>\n        Cras mattis consectetur purus sit amet fermentum. Cras justo odio, dapibus ac facilisis in,\n        egestas eget quam. Morbi leo risus, porta ac consectetur ac, vestibulum at eros.\n      </p>\n      <p>\n        <button type=\"button\" className=\"btn btn-primary\">\n          Primary\n        </button>{' '}\n        <button type=\"button\" className=\"btn btn-secondary\">\n          Secondary\n        </button>{' '}\n        <button type=\"button\" className=\"btn btn-success\">\n          Success\n        </button>{' '}\n        <button type=\"button\" className=\"btn btn-danger\">\n          Danger\n        </button>{' '}\n        <button type=\"button\" className=\"btn btn-warning\">\n          Warning\n        </button>{' '}\n        <button type=\"button\" className=\"btn btn-info\">\n          Info\n        </button>{' '}\n      </p>\n      <p>\n        Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Vivamus sagittis lacus\n        vel augue laoreet rutrum faucibus dolor auctor.\n      </p>\n      <div style={{ display: '' }}>\n        <p>\n          Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus magna, vel\n          scelerisque nisl consectetur et. Donec sed odio dui. Donec ullamcorper nulla non metus\n          auctor fringilla.\n        </p>\n        <p>\n          Cras mattis consectetur purus sit amet fermentum. Cras justo odio, dapibus ac facilisis\n          in, egestas eget quam. Morbi leo risus, porta ac consectetur ac, vestibulum at eros.\n        </p>\n        <p>\n          Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Vivamus sagittis lacus\n          vel augue laoreet rutrum faucibus dolor auctor.\n        </p>\n        <p>\n          Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus magna, vel\n          scelerisque nisl consectetur et. Donec sed odio dui. Donec ullamcorper nulla non metus\n          auctor fringilla.\n        </p>\n        <p>\n          Cras mattis consectetur purus sit amet fermentum. Cras justo odio, dapibus ac facilisis\n          in, egestas eget quam. Morbi leo risus, porta ac consectetur ac, vestibulum at eros.\n        </p>\n        <p>\n          Praesent commodo cursus magna, vel scelerisque nisl consectetur et. Vivamus sagittis lacus\n          vel augue laoreet rutrum faucibus dolor auctor.\n        </p>\n        <p>\n          Aenean lacinia bibendum nulla sed consectetur. Praesent commodo cursus magna, vel\n          scelerisque nisl consectetur et. Donec sed odio dui. Donec ullamcorper nulla non metus\n          auctor fringilla.\n        </p>\n      </div>\n    </Dialog>\n  );\n\n  return (\n    <div style={{ margin: 20 }}>\n      <p>\n        <button type=\"button\" className=\"btn btn-primary\" onClick={onClick}>\n          show dialog\n        </button>\n        &nbsp;\n        <label>\n          destroy on hidden:\n          <input type=\"checkbox\" checked={destroyOnHidden} onChange={onDestroyOnHiddenChange} />\n        </label>\n      </p>\n      {dialog}\n    </div>\n  );\n};\n\nexport default MyControl;\n";;
  