
  import '/Users/afc163/Projects/dialog/docs/examples/multiple-Portal.tsx?watch=parent';
  export default "import * as React from 'react';\nimport Drawer from '@rc-component/drawer';\nimport '@rc-component/drawer/assets/index.css';\nimport Dialog from '@rc-component/dialog';\nimport '../../assets/index.less';\n\nconst Demo: React.FC = () => {\n  const [showDialog, setShowDialog] = React.useState(false);\n  const [showDrawer, setShowDrawer] = React.useState(false);\n\n  const onToggleDrawer = () => {\n    setShowDrawer((value) => !value);\n  };\n\n  const onToggleDialog = () => {\n    setShowDialog((value) => !value);\n  };\n\n  const dialog = (\n    <Dialog\n      visible={showDialog}\n      animation=\"zoom\"\n      maskAnimation=\"fade\"\n      onClose={onToggleDialog}\n      forceRender\n      title=\"basic modal\"\n    >\n      <p>\n        <button type=\"button\" onClick={onToggleDrawer}>\n          show drawer\n        </button>\n      </p>\n      <div style={{ height: 200 }} />\n    </Dialog>\n  );\n  const drawer = (\n    <Drawer open={showDrawer} onClose={onToggleDrawer}>\n      <button type=\"button\" onClick={onToggleDrawer}>\n        close drawer\n      </button>\n    </Drawer>\n  );\n  return (\n    <div>\n      <button type=\"button\" onClick={onToggleDialog}>\n        open dialog\n      </button>\n      <button\n        type=\"button\"\n        onClick={() => {\n          setShowDialog(true);\n          setTimeout(() => {\n            setShowDialog(false);\n          }, 0);\n        }}\n      >\n        quick\n      </button>\n      {dialog}\n      {drawer}\n    </div>\n  );\n};\n\nexport default Demo;\n";;
  