  export default "/* eslint no-console:0 */\nimport * as React from 'react';\nimport Select from '@rc-component/select';\nimport '@rc-component/select/assets/index.less';\nimport Dialog from '@rc-component/dialog';\nimport '../../assets/index.less';\n\nconst clearPath =\n  'M793 242H366v-74c0-6.7-7.7-10.4-12.9' +\n  '-6.3l-142 112c-4.1 3.2-4.1 9.4 0 12.6l142 112c' +\n  '5.2 4.1 12.9 0.4 12.9-6.3v-74h415v470H175c-4.4' +\n  ' 0-8 3.6-8 8v60c0 4.4 3.6 8 8 8h618c35.3 0 64-' +\n  '28.7 64-64V306c0-35.3-28.7-64-64-64z';\n\nconst getSvg = (path: string, props = {}, align = false) => (\n  <i {...props}>\n    <svg\n      viewBox=\"0 0 1024 1024\"\n      width=\"1em\"\n      height=\"1em\"\n      fill=\"currentColor\"\n      style={align ? { verticalAlign: '-0.125em ' } : {}}\n    >\n      <path d={path} />\n    </svg>\n  </i>\n);\n\nconst MyControl: React.FC = () => {\n  const [visible1, setVisible1] = React.useState(true);\n  const [visible2, setVisible2] = React.useState(false);\n  const [visible3, setVisible3] = React.useState(false);\n  const [width, setWidth] = React.useState(600);\n  const [destroyOnHidden, setDestroyOnHidden] = React.useState(false);\n  const [center, setCenter] = React.useState(false);\n  const [mousePosition, setMousePosition] = React.useState({ x: null, y: null });\n  const [useIcon, setUseIcon] = React.useState(false);\n  const [forceRender, setForceRender] = React.useState(false);\n\n  const onClick = (e: React.MouseEvent) => {\n    setMousePosition({ x: e.pageX, y: e.pageY });\n    setVisible1(true);\n  };\n\n  const onClose = () => {\n    setVisible1(false);\n  };\n\n  const onClose2 = () => {\n    setVisible2(false);\n  };\n\n  const onClose3 = () => {\n    setVisible3(false);\n  };\n\n  const closeAll = () => {\n    setVisible1(false);\n    setVisible2(false);\n    setVisible3(false);\n  };\n\n  const onDestroyOnHiddenChange = (e: React.ChangeEvent<HTMLInputElement>) => {\n    setDestroyOnHidden(e.target.checked);\n  };\n\n  const onForceRenderChange = (e: React.ChangeEvent<HTMLInputElement>) => {\n    setForceRender(e.target.checked);\n  };\n\n  const changeWidth = () => {\n    setWidth(width === 600 ? 800 : 600);\n  };\n\n  const centerEvent = (e: React.ChangeEvent<HTMLInputElement>) => {\n    setCenter(e.target.checked);\n  };\n\n  const toggleCloseIcon = () => {\n    setUseIcon(!useIcon);\n  };\n\n  const style = { width };\n\n  let wrapClassName = '';\n  if (center) {\n    wrapClassName = 'center';\n  }\n\n  const dialog = (\n    <Dialog\n      visible={visible1}\n      wrapClassName={wrapClassName}\n      animation=\"zoom\"\n      maskAnimation=\"fade\"\n      onClose={onClose}\n      style={style}\n      title=\"dialog1\"\n      mousePosition={mousePosition}\n      destroyOnHidden={destroyOnHidden}\n      closeIcon={useIcon ? getSvg(clearPath, {}, true) : undefined}\n      forceRender={forceRender}\n      focusTriggerAfterClose={false}\n    >\n      <input autoFocus />\n      <p>basic modal</p>\n      <button\n        type=\"button\"\n        onClick={() => {\n          setVisible1(false);\n          setVisible2(true);\n        }}\n      >\n        打开第二个并关闭当前的\n      </button>\n      <button\n        type=\"button\"\n        onClick={() => {\n          setVisible2(true);\n        }}\n      >\n        打开第二个\n      </button>\n      <button type=\"button\" onClick={changeWidth}>\n        change width\n      </button>\n      <button type=\"button\" onClick={toggleCloseIcon}>\n        use custom icon, is using icon: {(useIcon && 'true') || 'false'}.\n      </button>\n      <div style={{ height: 200 }}>\n        <Select popupStyle={{ zIndex: 9999999 }}>\n          <Select.Option value=\"light\">Light</Select.Option>\n        </Select>\n      </div>\n    </Dialog>\n  );\n\n  const dialog2 = (\n    <Dialog visible={visible2} title=\"dialog2\" onClose={onClose2}>\n      <input autoFocus />\n      <p>basic modal</p>\n      <button\n        type=\"button\"\n        onClick={() => {\n          setVisible3(true);\n        }}\n      >\n        打开第三个\n      </button>\n      <button\n        type=\"button\"\n        onClick={() => {\n          setVisible2(false);\n        }}\n      >\n        关闭当前\n      </button>\n      <button type=\"button\" onClick={closeAll}>\n        关闭所有\n      </button>\n      <button type=\"button\" onClick={changeWidth}>\n        change width\n      </button>\n      <button type=\"button\" onClick={toggleCloseIcon}>\n        use custom icon, is using icon: {(useIcon && 'true') || 'false'}.\n      </button>\n      <div style={{ height: 200 }} />\n    </Dialog>\n  );\n\n  const dialog3 = (\n    <Dialog forceRender title=\"dialog3\" visible={visible3} onClose={onClose3}>\n      <p>initialized with forceRender and visbile true</p>\n      <button\n        type=\"button\"\n        onClick={() => {\n          setVisible3(false);\n        }}\n      >\n        关闭当前\n      </button>\n      <button type=\"button\" onClick={closeAll}>\n        关闭所有\n      </button>\n      <button type=\"button\" onClick={changeWidth}>\n        change width\n      </button>\n      <button type=\"button\" onClick={toggleCloseIcon}>\n        use custom icon, is using icon: {(useIcon && 'true') || 'false'}.\n      </button>\n      <div style={{ height: 200 }} />\n    </Dialog>\n  );\n\n  return (\n    <React.StrictMode>\n      <div style={{ width: '90%', margin: '0 auto', height: '150vh' }}>\n        <style>\n          {`\n          .center {\n            display: flex;\n            align-items: center;\n            justify-content: center;\n          }\n          `}\n        </style>\n        <p>\n          <button type=\"button\" className=\"btn btn-primary\" onClick={onClick}>\n            show dialog\n          </button>\n          &nbsp;\n          <label>\n            destroy on hidden:\n            <input type=\"checkbox\" checked={destroyOnHidden} onChange={onDestroyOnHiddenChange} />\n          </label>\n          &nbsp;\n          <label>\n            center\n            <input type=\"checkbox\" checked={center} onChange={centerEvent} />\n          </label>\n          &nbsp;\n          <label>\n            force render\n            <input type=\"checkbox\" checked={forceRender} onChange={onForceRenderChange} />\n          </label>\n          <input placeholder=\"Useless Input\" onClick={onClick} />\n        </p>\n        {dialog}\n        {dialog2}\n        {dialog3}\n      </div>\n    </React.StrictMode>\n  );\n};\n\nexport default MyControl;\n";;
  